import React, { useEffect, useState } from 'react';
import { BlockMath, InlineMath } from 'react-katex';
import 'katex/dist/katex.min.css'; // Import KaTeX styles
import './App.css';

function App() {
  const [questions, setQuestions] = useState([]);
  const [selectedAnswer, setSelectedAnswer] = useState(null);
  const [feedback, setFeedback] = useState('');
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);

  useEffect(() => {
    async function fetchQuestions() {
      const response = await fetch('/questions');
      const data = await response.json();
      setQuestions(data);
    }

    fetchQuestions();
  }, []);

  const handleAnswerClick = (choice) => {
    const question = questions[currentQuestionIndex];
    setSelectedAnswer(choice);
    if (choice === question.correct_choice) {
      setFeedback('Correct!');
    } else {
      setFeedback('Incorrect. Try again.');
    }
  };

  const handleNextQuestion = () => {
    setSelectedAnswer(null);
    setFeedback('');
    setCurrentQuestionIndex((prevIndex) => (prevIndex + 1) % questions.length);
  };

  const question = questions[currentQuestionIndex];

  return (
    <div className="App">
      <header className="App-header">
        <h1>Math Questions</h1>
        {question && (
          <div className="question-container">
            <h2>
              <BlockMath math={question.question_text} />
            </h2>
            <div className="choices">
              {['A', 'B', 'C', 'D', 'E'].map((choice) => (
                <button
                  key={choice}
                  className={`choice-button ${selectedAnswer === question[`choice_${choice.toLowerCase()}`] ? 'selected' : ''}`}
                  onClick={() => handleAnswerClick(question[`choice_${choice.toLowerCase()}`])}
                >
                  {choice}. <InlineMath math={question[`choice_${choice.toLowerCase()}`]} />
                </button>
              ))}
            </div>
            {feedback && <p className="feedback">{feedback}</p>}
            <button onClick={handleNextQuestion} className="next-button">Next Question</button>
          </div>
        )}
      </header>
    </div>
  );
}

export default App;
